.nav {
  @include bg {
    position: fixed;
    left: 0;
    top: 0;
    padding: 40px;
    width: 100%;
    height: 100%;
    background: $white;
    transform: translateY(-200%);
    transition: transform 0.5s;

    &__list {
      flex-direction: column;
      font-size: 20px;
      gap: 30px;
    }
  }
}

.nav.active {
  transform: none;
}

.nav .nav__item {
  list-style-type: none;
}
