.hamburger {
  display: none;

  &-box {
    width: 30px;
  }

  &-inner {
    width: 30px;
    height: 2px;
    background-color: #000;

    &::after,
    &::before {
      width: 30px;
      height: 2px;
      background-color: #000;
    }
  }

  @include bg {
    z-index: 2;
    display: flex;
    padding: 5px;
  }
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::after,
.hamburger.is-active .hamburger-inner::before {
  background-color: #000;
}
