.footer {
  z-index: 2;
  padding: 90px 0 82px;
  background-color: $white;

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__info {
    max-width: 300px;
  }

  &__logo {
    margin-bottom: 18px;
  }

  &__descr {
    margin-bottom: 40px;
  }

  &-nav {
    display: flex;
    gap: 85px;

    &__title {
      margin-bottom: 18px;
      font-weight: 700;
      font-size: 21px;
      letter-spacing: 0.5px;
    }

    &__list {
      display: grid;
      gap: 13px;
    }

    &__item {
      font-size: 14px;
      letter-spacing: 0.5px;
    }

    &__link {
      transition: color 0.2s ease-in-out;

      &:hover {
        color: $gray;

        @include bg {
          @include hover {
            color: inherit;
          }
        }
      }
    }
  }
}

.social {
  display: flex;
  align-items: center;
  gap: 30px;

  img {
    transition: transform 0.2s ease-in-out;

    &:hover {
      transform: scale(1.2);
    }
  }
}
