.objects {
  padding-bottom: 155px;
  background-color: $white;

  &__container-fluid {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    gap: 30px;

    @include xxl {
      justify-content: flex-end;
    }

    @include xl {
      padding: 0 15px;
    }
  }

  &__images {
    display: flex;
    flex-shrink: 0;
    align-items: center;
    gap: 32px;
  }

  &__content {
    flex-shrink: 0;
    padding-right: clamp(0rem, -56.25rem + 75vw, 9.375rem);
    padding-left: clamp(3.125rem, -29.375rem + 43.33vw, 11.25rem);
    max-width: 374px;
    box-sizing: content-box;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__descr {
    margin-bottom: 40px;

    p {
      @include mb(20px);
    }
  }

  &__link {
    padding: 16px 39px;
  }
}
