.reviews-article {
  position: relative;
  border-radius: 5px;

  &__link {
    position: absolute;
    inset: 0;
  }

  &__image {
    position: relative;
  }

  &__btn {
    position: absolute;
    right: 20px;
    bottom: 20px;
    opacity: 0;
    transform: translateY(25%);
    transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
  }

  &__text {
    padding: 30px;
    background-color: $white;
  }

  &__blockquote {
    margin-bottom: 23px;
  }

  &__author {
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 0.6px;
  }

  &__city {
    color: $fountain-blue;
    transition: color 0.2s ease-in-out;
  }
}
