.benefits {
  padding-bottom: 110px;
  background-color: $white;

  &__title {
    margin-bottom: 58px;
  }

  &__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    &-link {
      position: absolute;
      inset: 0;
    }

    &-img {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 15px;
      border-radius: 100%;
      width: 70px;
      height: 70px;
      background-color: rgba($fountain-blue, 20%);
      transition: background-color 0.2s ease-in-out;

      svg {
        width: 39px;
        height: 30px;
        fill: $fountain-blue;
        transition: fill 0.2s ease-in-out;
      }
    }

    &-title {
      margin-bottom: 2px;
      font-weight: 700;
      font-size: 28px;
      line-height: 1.5;
      letter-spacing: 0.5px;
    }

    &:hover {
      .benefits__item-img {
        background-color: $accent;
      }

      svg {
        fill: $white;
      }
    }
  }
}
