:root {
  --font-family: "Poppins", sans-serif;
  --content-width: 1110px;
  --content-width-fluid: 100%;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width-fluid: calc(var(--content-width-fluid) + (var(--container-offset) * 2));
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable value-keyword-case */
@font-face {
  font-family: "Poppins";
  src: url("../fonts/../fonts/Poppins-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/../fonts/Poppins-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
/* stylelint-disable declaration-no-important */
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  overflow-x: hidden;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: #323232;
  background-color: #fff;
}

.section-title {
  font-weight: 700;
  font-size: 42px;
  line-height: 1.4;
  letter-spacing: 0.8px;
}

.text {
  letter-spacing: 1.15px;
}

blockquote {
  margin: 0;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover,
a:visited,
a:link,
a:active {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

:where(ul, ol) {
  list-style: none;
  margin: 0;
  padding: 0;
}

.site-container {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-fluid {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width-fluid);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  padding: clamp(1.25rem, 0.87rem + 1.9vw, 2.188rem) 0;
  background-color: #fff;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;
  cursor: pointer;
  gap: 18px;
}
.logo span {
  font-weight: 700;
  font-size: 24px;
  line-height: 1;
}

.nav__list {
  display: flex;
  align-items: center;
  gap: 42px;
}
.nav__link {
  transition: color 0.2s ease-in-out;
}
.nav__link:hover {
  color: #ef9656;
}
.nav__link:active {
  font-weight: 700;
  color: #ef9656;
}
@media (max-width: 860px) {
  .nav__link:hover {
    color: inherit;
  }
}

.hamburger {
  display: none;
}
.hamburger-box {
  width: 30px;
}
.hamburger-inner {
  width: 30px;
  height: 2px;
  background-color: #000;
}
.hamburger-inner::after, .hamburger-inner::before {
  width: 30px;
  height: 2px;
  background-color: #000;
}
@media (max-width: 860px) {
  .hamburger {
    z-index: 2;
    display: flex;
    padding: 5px;
  }
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::after,
.hamburger.is-active .hamburger-inner::before {
  background-color: #000;
}

@media (max-width: 860px) {
  .nav {
    position: fixed;
    left: 0;
    top: 0;
    padding: 40px;
    width: 100%;
    height: 100%;
    background: #fff;
    transform: translateY(-200%);
    transition: transform 0.5s;
  }
  .nav__list {
    flex-direction: column;
    font-size: 20px;
    gap: 30px;
  }
}

.nav.active {
  transform: none;
}

.nav .nav__item {
  list-style-type: none;
}

.hero {
  background-color: #fff;
}
.hero__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 30px;
}
.hero__content {
  grid-column: 6 span;
  padding-top: 127px;
}
.hero__title {
  position: relative;
  z-index: 2;
  margin-bottom: 25px;
  width: 674px;
  font-weight: 700;
  font-size: 54px;
  line-height: 1.4;
  letter-spacing: 0.8px;
}
.hero__descr {
  margin-bottom: 42px;
  max-width: 467px;
}
.hero__image {
  grid-column: 6 span;
  height: 676px;
}
.hero__img {
  border-radius: 40px 40px 40px 0;
}

.form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 29px;
  height: 58px;
  max-width: 460px;
  box-shadow: 0 17px 42px 0 rgba(0, 0, 0, 0.12);
  background: #fff;
  transition: box-shadow 0.2s ease-in-out;
  gap: 20px;
}
.form__label {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 72px;
  width: 100%;
}
.form__label::before {
  content: "";
  position: absolute;
  left: 28px;
  width: 24px;
  height: 19px;
  background-image: url("../img/icons/envelope.svg");
}
.form__input {
  flex-grow: 1;
  outline: none;
  padding-right: 15px;
  font-weight: 400;
  font-size: 16px;
}
.form__input::placeholder {
  font-weight: 400;
  font-size: 16px;
  color: #bbb;
}
.form__btn {
  padding: 20px 37px;
}
.form--active {
  box-shadow: 0 17px 42px 0 rgba(247, 132, 52, 0.27);
}

.btn {
  display: inline-block;
  border-radius: 29px;
  font-weight: 700;
  font-size: 16px;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}
.btn--primary {
  color: #fff;
  background-color: #ef9656;
}
.btn--primary:hover {
