}
.btn--secondary {
  color: #323232;
  background-color: #fff;
}
.btn--secondary:hover {
  color: #323232;
  background-color: #e6ebec;
}

.partners {
  padding: 100px 0 90px;
  background-color: #fff;
}
.partners__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
}

.objects {
  padding-bottom: 155px;
  background-color: #fff;
}
.objects__container-fluid {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  gap: 30px;
}
@media (max-width: 1400px) {
  .objects__container-fluid {
    justify-content: flex-end;
  }
}
@media (max-width: 1200px) {
  .objects__container-fluid {
    padding: 0 15px;
  }
}
.objects__images {
  display: flex;
  flex-shrink: 0;
  align-items: center;
