}
.objects__content {
  flex-shrink: 0;
  padding-right: clamp(0rem, -56.25rem + 75vw, 9.375rem);
  padding-left: clamp(3.125rem, -29.375rem + 43.33vw, 11.25rem);
  max-width: 374px;
  box-sizing: content-box;
}
.objects__title {
  margin-bottom: 20px;
}
.objects__descr {
  margin-bottom: 40px;
}
.objects__descr p:not(:last-child) {
  margin-bottom: 20px;
}
.objects__link {
  padding: 16px 39px;
}

.card {
  position: relative;
}
.card-info {
  position: absolute;
  right: -46px;
  bottom: 87px;
  display: flex;
  align-items: center;
  border-radius: 15px;
  padding: 32px 29px;
  max-width: 300px;
  box-shadow: 4px 20px 42px -2px rgba(0, 0, 0, 0.12);
