  gap: 20px;
}
.card-info__icon {
  flex-shrink: 0;
}
.card-info__title {
  font-weight: 700;
  letter-spacing: 0.8px;
}
.card img {
  border-radius: 20px;
}
.card--rounded img {
  border-radius: 20px 20px 20px 0;
}

.cta {
  padding-bottom: 100px;
}
.cta__block {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  border-radius: 20px;
  background-color: #58bebf;
}
.cta__image {
  margin-top: 60px;
  border-radius: 0 20px 0 0;
  padding: 25px 25px 0 0;
  box-shadow: 4px 20px 42px -2px rgba(0, 0, 0, 0.12);
  background-color: #fff;
}
.cta__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 90px 95px;
  width: 556px;
  color: #fff;
  background: url("../img/cta/decor-right.svg") right top no-repeat, url("../img/cta/decor-bottom.svg") bottom left -186px no-repeat;
}
.cta__title {
  margin-bottom: 15px;
}
.cta__descr {
  margin-bottom: 30px;
}
.cta__btns {
  display: flex;
  gap: 20px;
}
.cta__link {
  padding: 16px 41px;
}
.cta__btn {
  position: relative;
  padding: 16px 30px 16px 50px;
}
.cta__btn::before {
  content: "";
  position: absolute;
  left: 30px;
  top: 22px;
  width: 12px;
  height: 12px;
  background-image: url("../img/icons/play.svg");
  background-repeat: no-repeat;
}

.serve {
  position: relative;
  padding-bottom: 120px;
}
.serve::before {
  content: "";
  position: absolute;
  left: 0;
  top: -50%;
  bottom: 50%;
  z-index: -1;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #f5f6fa;
}
.serve__title {
  margin-bottom: 10px;
}
.serve__descr {
  margin-bottom: 45px;
}
.serve__inner {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.serve__card {
  position: relative;
  border-radius: 10px;
  padding: 40px 88px;
  box-shadow: 0 40px 52px -22px rgba(0, 0, 0, 0.08);
