  z-index: 5;
  padding: 16px 39px;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
}
.serve__card svg {
  margin-bottom: 15px;
  width: 70px;
  height: 70px;
  fill: #ef9656;
  transition: fill 0.2s ease-in-out;
}
.serve__card svg:hover {
  fill: #fff;
}
.serve__card:hover {
  color: #fff;
  background-color: #ef9656;
}
.serve__card:hover svg {
  fill: #fff;
}
.serve__card:hover .section-title {
  color: #fff;
}
.serve__card:hover .btn {
  color: #ef9656;
  background-color: #fff;
}
.serve__card:hover .btn:hover {
  background-color: #e6ebec;
}

.benefits {
  padding-bottom: 110px;
  background-color: #fff;
}
.benefits__title {
  margin-bottom: 58px;
}
.benefits__inner {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}
.benefits__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.benefits__item-link {
  position: absolute;
  inset: 0;
}
.benefits__item-img {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
  border-radius: 100%;
  width: 70px;
  height: 70px;
  background-color: rgba(88, 190, 191, 0.2);
  transition: background-color 0.2s ease-in-out;
}
.benefits__item-img svg {
