}
.benefits__item:hover svg {
  fill: #fff;
}

.reviews {
  padding: 60px 0;
  background-color: #fff;
}
.reviews__header {
  display: grid;
  align-items: center;
  grid-template-columns: 5fr 2fr 4fr;
  margin-bottom: 60px;
  gap: 20px;
}
.reviews__descr {
  grid-column-start: 3;
  color: #606060;
}
.reviews__list {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}
.reviews__list::after {
  content: "";
  position: absolute;
  right: -40px;
  top: -40px;
  width: 80px;
  height: 80px;
  background-image: url("../img/reviews/decor-logo.svg");
