}
.reviews__item:hover .reviews-article__city {
  color: #ef9656;
}
.reviews__item:hover .reviews-article__btn {
  opacity: 1;
  transform: translateY(0%);
}

.reviews-article {
  position: relative;
  border-radius: 5px;
}
.reviews-article__link {
  position: absolute;
  inset: 0;
}
.reviews-article__image {
  position: relative;
}
.reviews-article__btn {
  position: absolute;
  right: 20px;
  bottom: 20px;
  opacity: 0;
  transform: translateY(25%);
  transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
}
.reviews-article__text {
  padding: 30px;
  background-color: #fff;
}
.reviews-article__blockquote {
