  transition: color 0.2s ease-in-out;
}

.contacts {
  position: relative;
  padding: 200px 0 55px;
  background-image: url("../img/contacts/contacts-bg.svg");
  background-size: cover;
  background-color: #fff;
}
.contacts::before {
  content: "";
  position: absolute;
  left: 50%;
  margin-left: -80px;
  width: 743px;
  height: 743px;
  background-image: url("../img/contacts/contacts-oval.svg");
  background-size: initial;
  background-repeat: no-repeat;
}
.contacts::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  width: 478px;
  height: 547px;
  background-image: url("../img/contacts/girl.png");
  background-size: initial;
  background-repeat: no-repeat;
}
.contacts__info {
