.footer {
  z-index: 2;
  padding: 90px 0 82px;
  background-color: #fff;
}
.footer__container {
  display: flex;
  justify-content: space-between;
}
.footer__info {
  max-width: 300px;
}
.footer__logo {
  margin-bottom: 18px;
}
.footer__descr {
  margin-bottom: 40px;
}
.footer-nav {
  display: flex;
  gap: 85px;
}
.footer-nav__title {
  margin-bottom: 18px;
  font-weight: 700;
  font-size: 21px;
  letter-spacing: 0.5px;
}
.footer-nav__list {
  display: grid;
  gap: 13px;
}
.footer-nav__item {
  font-size: 14px;
  letter-spacing: 0.5px;
}
.footer-nav__link {
