.hero {
  background-color: $white;

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
  }

  &__content {
    grid-column: 6 span;
    padding-top: 127px;
  }

  &__title {
    position: relative;
    z-index: 2;
    margin-bottom: 25px;
    width: 674px;
    font-weight: 700;
    font-size: 54px;
    line-height: 1.4;
    letter-spacing: 0.8px;
  }

  &__descr {
    margin-bottom: 42px;
    max-width: 467px;
  }

  &__image {
    grid-column: 6 span;
    height: 676px;
  }

  &__img {
    border-radius: 40px 40px 40px 0;
  }
}
