.btn {
  display: inline-block;
  border-radius: 29px;
  font-weight: 700;
  font-size: 16px;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;

  &--primary {
    color: $white;
    background-color: $accent;

    &:hover {
      color: $white;
      background-color: $accent-hover;
    }
  }

  &--secondary {
    color: $black;
    background-color: $white;

    &:hover {
      color: $black;
      background-color: #e6ebec;
    }
  }
}
