.serve {
  position: relative;
  padding-bottom: 120px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: -50%;
    bottom: 50%;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
    background-color: #f5f6fa;
  }

  &__title {
    margin-bottom: 10px;
  }

  &__descr {
    margin-bottom: 45px;
  }

  &__inner {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
}
