.serve__card {
  position: relative;
  border-radius: 10px;
  padding: 40px 88px;
  box-shadow: 0 40px 52px -22px rgb(0 0 0 / 8%);
  text-align: center;
  background-color: $white;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;

  &-link {
    position: absolute;
    inset: 0;
    width: 100%;
  }

  &-text {
    margin-bottom: 25px;
  }

  &-btn {
    position: relative;
    z-index: 5;
    padding: 16px 39px;
    transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
  }

  svg {
    margin-bottom: 15px;
    width: 70px;
    height: 70px;
    fill: $accent;
    transition: fill 0.2s ease-in-out;

    &:hover {
      fill: $white;
    }
  }

  &:hover {
    color: $white;
    background-color: $accent;

    svg {
      fill: $white;
    }

    .section-title {
      color: $white;
    }

    .btn {
      color: $accent;
      background-color: $white;

      &:hover {
        background-color: #e6ebec;
      }
    }
  }
}
