.contacts {
  position: relative;
  padding: 200px 0 55px;
  background-image: url("../img/contacts/contacts-bg.svg");
  background-size: cover;
  background-color: $white;

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    margin-left: -80px;
    width: 743px;
    height: 743px;
    background-image: url("../img/contacts/contacts-oval.svg");
    background-size: initial;
    background-repeat: no-repeat;
  }

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 478px;
    height: 547px;
    background-image: url("../img/contacts/girl.png");
    background-size: initial;
    background-repeat: no-repeat;
  }

  &__info {
    max-width: 360px;
    color: $white;
  }

  &__title {
    margin-bottom: 15px;
  }

  &__descr {
    margin-bottom: 30px;
  }

  &__link {
    padding: 16px 41px;
    text-transform: capitalize;
  }
}
