.header {
  padding: clamp(1.25rem, 0.87rem + 1.9vw, 2.188rem) 0;
  background-color: $white;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.logo {
  display: flex;
  align-items: center;
  cursor: pointer;
  gap: 18px;

  span {
    font-weight: 700;
    font-size: 24px;
    line-height: 1;
  }
}
