.cta {
  padding-bottom: 100px;

  &__block {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    border-radius: 20px;
    background-color: $fountain-blue;
  }

  &__image {
    margin-top: 60px;
    border-radius: 0 20px 0 0;
    padding: 25px 25px 0 0;
    box-shadow: 4px 20px 42px -2px rgb(0 0 0 / 12%);
    background-color: $white;
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 90px 95px;
    width: 556px;
    color: $white;
    background:
      url("../img/cta/decor-right.svg") right top no-repeat,
      url("../img/cta/decor-bottom.svg") bottom left -186px no-repeat;
  }

  &__title {
    margin-bottom: 15px;
  }

  &__descr {
    margin-bottom: 30px;
  }

  &__btns {
    display: flex;
    gap: 20px;
  }

  &__link {
    padding: 16px 41px;
  }

  &__btn {
    position: relative;
    padding: 16px 30px 16px 50px;

    &::before {
      content: "";
      position: absolute;
      left: 30px;
      top: 22px;
      width: 12px;
      height: 12px;
      background-image: url("../img/icons/play.svg");
      background-repeat: no-repeat;
    }
  }
}
