.nav {
  &__list {
    display: flex;
    align-items: center;
    gap: 42px;
  }

  &__link {
    transition: color 0.2s ease-in-out;

    &:hover {
      color: $accent;
    }

    &:active {
      font-weight: 700;
      color: $accent;
    }

    @include bg {
      &:hover {
        color: inherit;
      }
    }
  }
}
