.form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 29px;
  height: 58px;
  max-width: 460px;
  box-shadow: 0 17px 42px 0 rgb(0 0 0 / 12%);
  background: #fff;
  transition: box-shadow 0.2s ease-in-out;
  gap: 20px;

  &__label {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 72px;
    width: 100%;

    &::before {
      content: "";
      position: absolute;
      left: 28px;
      width: 24px;
      height: 19px;
      background-image: url("../img/icons/envelope.svg");
    }
  }

  &__input {
    flex-grow: 1;
    outline: none;
    padding-right: 15px;
    font-weight: 400;
    font-size: 16px;

    &::placeholder {
      font-weight: 400;
      font-size: 16px;
      color: $gray;
    }
  }

  &__btn {
    padding: 20px 37px;
  }

  &--active {
    box-shadow: 0 17px 42px 0 rgb(247 132 52 / 27%);
  }
}
