.reviews {
  padding: 60px 0;
  background-color: $white;

  &__header {
    display: grid;
    align-items: center;
    grid-template-columns: 5fr 2fr 4fr;
    margin-bottom: 60px;
    gap: 20px;
  }

  &__descr {
    grid-column-start: 3;
    color: #606060;
  }

  &__list {
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;

    &::after {
      content: "";
      position: absolute;
      right: -40px;
      top: -40px;
      width: 80px;
      height: 80px;
      background-image: url("../img/reviews/decor-logo.svg");
      background-repeat: no-repeat;
    }
  }

  &__item {
    max-width: 350px;
    transition: box-shadow 0.2s ease-in-out;

    &:hover {
      box-shadow: 0 10px 52px 0 rgb(0 0 0 / 8%);

      .reviews-article__city {
        color: $accent;
      }

      .reviews-article__btn {
        opacity: 1;
        transform: translateY(0%);
      }
    }
  }
}
