.card {
  position: relative;

  &-info {
    position: absolute;
    right: -46px;
    bottom: 87px;
    display: flex;
    align-items: center;
    border-radius: 15px;
    padding: 32px 29px;
    max-width: 300px;
    box-shadow: 4px 20px 42px -2px rgb(0 0 0 / 12%);
    background-color: $white;
    gap: 20px;

    &__icon {
      flex-shrink: 0;
    }

    &__title {
      font-weight: 700;
      letter-spacing: 0.8px;
    }
  }

  img {
    border-radius: 20px;
  }

  &--rounded {
    img {
      border-radius: 20px 20px 20px 0;
    }
  }
}
